.trending {
    color: white;
    font-size: 32px;
    font-weight: 300;
    margin-top: 3vh;
    margin-left: 25vh;
}

.trendingContainer {
    display: flex;
    overflow-x: auto;
    gap: 16px;
    padding: 20px;
    margin-left: 24vh;
}

.trendingItem {
    position: relative;
    min-width: 470px;
    height: 230px;
    border-radius: 8px;
    overflow: hidden;
}

.thumbnailContainer {
    position: relative;
    width: 100%;
    height: 100%;
}

.thumbnail {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
}

.overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 20px;
    background: linear-gradient(to top, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 100%);
}

.itemInfo {
    color: white;
}

.itemMeta {
    display: flex;
    gap: 8px;
    opacity: 0.75;
    font-size: 14px;
}

.itemTitle {
    margin-top: 8px;
    font-size: 24px;
    font-weight: 500;
}

.loading, .error {
    color: white;
    margin-left: 25vh;
    margin-top: 3vh;
    font-size: 18px;
}

.bookmarkIcon {
    position: absolute;
    right: 10px;
    top: 12px;
    margin-right: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    width: 40px;
    height: 40px;
    border-radius: 50%; /* Makes the background perfectly circular */
    cursor: pointer;
    transition: transform 0.2s ease-in-out;
    display: flex;
    align-items: center;
    justify-content: center;
}

.bookmarkIcon:hover {
    transform: scale(1.1);
    background-color: rgba(0, 0, 0, 0.7);
}

